.App {
  text-align: center;
  padding: 20px;
  
}

/* App.css or your custom CSS file */
.search-bar {
  
  display: flex;
  align-items: center;
  gap: 10px;
  margin:12px;
  background-color: #eff2f9;
  height:60px;
  border-radius: 12px;
  border:1px solid white;
}

.search-bar>button{
  text-decoration: none;
  color:black;
  border:none;
  background-color: #eff2f9;
}

.search-input-container {
 

  display: flex;
  align-items: center;
  border: none;
  border-radius: 5px;
  padding: 5px;
  margin:12px;
  width: 300px; /* Adjust the width as needed */
  
}

input[type="text"] {
  border:none;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  outline: none;
  width: 100%;

}
input[type="text"]::placeholder {
  padding-left: 10px; /* Add left padding to the placeholder text */
  
}
.search-icon {
  background-color: white;
  color: #333; /* Set the color for the search icon */
  cursor: pointer;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;

  padding-top:8px;
  padding-bottom: 8px;
  padding-right: 8px;
}

.arrow-down-icon {
  color: #333; /* Set the color for the arrow-down icon */
  margin-left: 5px; /* Adjust the margin as needed */
}


/* App.css or your custom CSS file */
.car-card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* This will create the desired layout */
  gap: 3px; /* Add some spacing between cards */
  margin:12px;
}

.car-card {
  width:380px; /* Adjust the width for two cards in a row */
  margin-bottom: 20px; /* Add some margin at the bottom of each card */
  padding: 18px;
  border: 1px solid white;
  border-radius: 12px;
  text-align: center;
  background-color:#f0f4fb;
}

.car-card img {
  border-radius: 12px;
  max-width: 100%;
  height: 250px; /* Adjust the height as needed */

}





.pagination {
  height:50px;
  border-radius: 12px;
  background-color: #edf1f7;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 12px;
  gap:600px;
  border:1px solid white;
 
}

.pagination a {
  border-radius: 5px;
  padding: 5px 10px;
  margin: 0 5px;
  text-decoration: none;
  color: black;
  background-color: white;
}

.pagination .active {
  background-color: #007bff;
  color: black;
  border-radius: 5px;
}


.car-details{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top:6px;
}

.year{
  width:50px;
  border-radius: 30px;
  border:1px dashed #9eccf3;
  align-items: center;
  padding:8px;
  font-weight: bold;
}


.car-name{
  font-size:24px;
  font-weight: 500;
}

table>tr{
  text-align: left;
}
table>tr>td{
  
  width:30%;
  padding:5px;

}
table>tr>td>span{
  margin-left:3px;
}


.white-icon{
  color: #5da5ee;
}

.car-price{
  display: flex;
  align-items: center;
  height:70px;
  border-top:1px solid #e8ecf4;

  justify-content: space-between;
}

.price>div{
  font-size:24px;
  display: inline;
  padding-right:3px;
}

.rent-button{
  background-color: #4999ec;
  color:white;
  border:none;
  border-radius:12px;
  padding:12px;
  font-weight: 600;
}


.rent{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.heart{
  margin-right:12px;
  padding:10px;
  background-color:#dbeafa;
  display: flex;
  align-items: center;
  border-radius:12px;

}
.h{
  color:#77b7f2;
}