body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #edf1f6;
  
}
/* Hide the scrollbar for webkit-based browsers (e.g., Chrome) */
body::-webkit-scrollbar {
  width: 0.5rem; /* Adjust the width as needed */
}

body::-webkit-scrollbar-track {
  background: transparent; /* Make the scrollbar track transparent */
}

body::-webkit-scrollbar-thumb {
  background-color: transparent; /* Make the scrollbar thumb transparent */
  border-radius: 9999px; /* Round the thumb to hide it */
}

/* Ensure smooth scrolling behavior */
body {
  scrollbar-width: thin; /* For Firefox */
  scrollbar-color: transparent transparent; /* For Firefox */
  -ms-overflow-style: none; /* For Internet Explorer and Edge */
  scrollbar-width: none; /* For Internet Explorer and Edge */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
